/*i have 3 icon favicon icons in html that needs to be centered on midle of the footer*/

#favicon:hover {
  cursor: pointer;
}

#flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

/* I want to have animations for my six different #animate-card containers in Portfolio Component */

#animate-card {
  opacity: 0.2;
}

#animate-card:hover {
  opacity: 0.9;
}

/* make my cards bounce up and down when Portfolio page is loaded */

#animate-card {
  -webkit-animation-name: spin;
  animation-name: spin;
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
}

