@tailwind base;
@tailwind components;
@tailwind utilities;

main {
  margin: 0 auto;
  max-width: 90%;
  padding: 50px 30px;
}
:root {
  --primary: #8c54ff;
  --secondary: #f3a847;
  --tertiary: #b12704;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --card: #000000;
  --card1: #f3a847;
  --background2: rgb(20, 20, 20);
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: 0.1em;
  background: var(--background2);
  color: var(--light);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
}

header {
  background: var(--dark);
}

header a {
  color: var(--light);
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);
}

button:hover {
  opacity: 0.8;
}

button[disabled] {
  cursor: default;
  opacity: 0.8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

/* #dev-image {
  width: 30%;
  height: 300px;
} */

#dev-image {
  height: 300px;
  width: 50%;
  margin-bottom: 20px;
  border-radius: 15px;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

li {
  list-style: none;
}

nav a:hover {
  text-decoration: none;
}

nav li {
  cursor: pointer;
}

textarea {
  width: 600px;
  height: 120px;
  border: 3px solid #cccccc;
  padding: 5px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}
#font-size {
  font-size: 1.89rem;
}

#footer-center {
  flex-direction: row;
  justify-content: center;
}

.center {
  align-items: center;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 40%;
  margin: 5;
  text-align: center;
}

.form-input {
  color: black;
}

#card1 {
  background-color: var(--card1);
}

#bgclr {
  background-color: var(--card);
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

.modalBackdrop {
  background: rgba(0, 0, 0, 0.65);
  bottom: 0;
  left: 0;
  overflow: auto;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1;
}

.modalContainer {
  background: var(--darker);
  border-radius: 5px;
  max-width: 100%;
  margin: 50px auto;
  padding: 15px;
  width: 960px;
}

.modalTitle {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 25px;
}

button {
  background: var(--primary);
  border: 0;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 30px;
  padding: 10px 15px;
}

a {
  border-radius: 5px;
  color: var(--primary);
  font-weight: 600;
  padding: 2px 4px;
  margin-left: -2px;
  margin-right: -2px;
}

.navActive {
  color: var(--secondary);
}

#name-style {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 10px;
}

#about {
  color: var(--primary);
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 50px;
}

#icon {
  margin-right: 10px;
  color: var(--primary);
}

#nav-fix {
  margin-right: 20px;
}

#tech-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--primary);
}

#state-title {
  font-size: 2rem;
  font-weight: bold;
  color: var(--primary);
}

#card-image {
  border: oldlace;
}

#card-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--secondary);
}

#card-text {
  font-size: 1.1rem;
  color: var(--secondary);
}

#github-link {
  font-size: 1.1rem;
  color: var(--secondary);
}

#deployed-link {
  font-size: 1.1rem;
  color: var(--secondary);
}

#deployed-link::hover {
  font-size: 1.1rem;
  color: var(--secondary);
}

#contact-title {
  font-size: 2rem;
  font-weight: bold;
  color: var(--primary);
  margin-bottom: 50px;
}

#name,
#email,
#message {
  font-size: 1.1rem;
  color: var(--secondary);
}

#resume-title {
  font-size: 2rem;
  font-weight: bold;
  color: var(--primary);
}

#resume-download::hover {
  font-size: 1.1rem;
  color: var(--secondary);
}

#orange {
  color: var(--secondary);
}

#orange:hover {
  color: var(--primary);
}

#color-4-download:hover {
  color: var(--secondary);
}

#front-end-title,
#back-end-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--secondary);
}

/* when #github-link and #deployed-link is hovered it changes opacity*/
#github-link:hover,
#deployed-link:hover {
  opacity: 0.8;
  color: var(--primary);
}

/*i have 3 icon favicon icons in html that needs to be centered on midle of the footer*/
#favicon:hover {
  cursor: pointer;
}

#flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

/* I want to have animations for my six different #animate-card containers in Portfolio Component */
#animate-card {
  opacity: 0.2;
}
#animate-card:hover {
  opacity: 0.9;
}

/* make my cards bounce up and down when Portfolio page is loaded */
#animate-card {
  animation-name: spin;
  animation-duration: 0.5s;
  animation-iteration-count: 1;
}
